@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap);

.coin-container {
  display: flex;
  justify-content: center;
  border: 1px solid #d7d7d7;
  padding: 10px;
}

.coin-row {
  display: flex;
  flex-direction: row;
  justify-items: start;
  align-items: center;
  height: 80px;
  width: 950px;
}
.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}
.coin-text {
  margin-top: 10px;
  margin-bottom: 0;
  font-weight: 400;
}
.coin h1 {
  font-size: 1rem;
  width: 190px;
  margin-left: 10px;
}
.coin img {
  height: 40px;
  width: 40px;
  margin-right: 10px;
}
.coin-symbol {
  text-transform: uppercase;
}
.coin-data {
  display: flex;
  text-align: center;
  justify-content: space-between;
}
.coin-price {
  width: 110px;
}

.coin-volume {
  width: 200px;
}
.coin-marketcap {
  width: 250px;
}
.coin-percent {
  width: 80px;
}
.red {
  color: firebrick;
}
.green {
  color: forestgreen;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #1a1a1a;
  color: white;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: white;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.coin-text {
  margin-bottom: 32px;
  text-align: center;
}
.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 5px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

